
body {
  background: bone;
}

.container {
  margin: 0px auto;
  padding: 2%;
  max-width: 1048px;
}

.card {
  margin: 2%;
}

input {
  max-width: 200px;
  border: 1px solid grey;
}

button {
  background-color: slateblue;
  color: #ffffff;
  margin: .5em auto;
}

.album_art {
  width: 100px;
  height: auto;
}

ul#results {
  overflow: auto;
  padding-left: 0px;
  margin: 0 1em;;

  li.card {
    list-style: none;
    width: 200px;
    height: 200px;
    padding: .4em;
    overflow: hidden;
    text-align: center;
    float: left;
    background-color: #ddd;
  }
}
